import { Card, Typography, Box, Button, Chip } from '@mui/material';
import Tooltip from '@mui/material/Tooltip';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import WarningAmberIcon from '@mui/icons-material/WarningAmber';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import ReportProblemIcon from '@mui/icons-material/ReportProblem';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import { Analysis, Message, Note, VALUE_PRIORITY_MAP } from '../conversation/types';
import { useConversation } from '../conversation/context';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';

const STATUS_MAP = {
    0: "#697A21", // green
    1: "#CC6F33", // darker yellow
    2: "#C52233", // red
    3: "#595F72", // gray
}

const MAX_WIDTH = 1000;

const capitalizeFirstLetter = (string: string | undefined) => {
    return string ? string.charAt(0).toUpperCase() + string.slice(1) : '';
};

export function getColor(analysis: Analysis | undefined): {
    correctColor: string,
    completeColor: string,
    hallucinationColor: string,
    // failureColor: string,
    totalColor: string
} {
    if (!analysis) {
        return {
            correctColor: STATUS_MAP[3],
            completeColor: STATUS_MAP[3],
            hallucinationColor: STATUS_MAP[3],
            // failureColor: STATUS_MAP[3],
            totalColor: STATUS_MAP[3],
        };
    }

    const correctValue = VALUE_PRIORITY_MAP[analysis.correct as keyof typeof VALUE_PRIORITY_MAP] as keyof typeof STATUS_MAP;
    const completeValue = VALUE_PRIORITY_MAP[analysis.complete as keyof typeof VALUE_PRIORITY_MAP] as keyof typeof STATUS_MAP;
    const hallucinatingValue = VALUE_PRIORITY_MAP[analysis.hallucinating as keyof typeof VALUE_PRIORITY_MAP] as keyof typeof STATUS_MAP;
    // const failureValue = VALUE_PRIORITY_MAP[analysis.failure_case as keyof typeof VALUE_PRIORITY_MAP] as keyof typeof STATUS_MAP;

    const value = Math.max(correctValue, completeValue, hallucinatingValue);

    const correctColor: string = STATUS_MAP[correctValue as keyof typeof STATUS_MAP];
    const completeColor: string = STATUS_MAP[completeValue as keyof typeof STATUS_MAP];
    const hallucinationColor: string = STATUS_MAP[hallucinatingValue as keyof typeof STATUS_MAP];
    // const failureColor: string = STATUS_MAP[failureValue as keyof typeof STATUS_MAP];
    const totalColor: string = STATUS_MAP[value as keyof typeof STATUS_MAP];

    return { correctColor, completeColor, hallucinationColor, totalColor };
    // return { correctColor, completeColor, hallucinationColor, failureColor, totalColor };
}

const getSeverityIndex = (severity: string) => {
    switch (severity) {
        case 'good':
        case "unlikely":
            return 0;
        case 'warning':
        case "possible":
            return 1;
        case 'error':
        case "likely":
            return 2;
        case 'unknown':
            return 3;
        default:
            return 3;
    }
}

const AnalysisCard = ({ title, value, icon, color }: { title: string, value: string | undefined, icon: JSX.Element, color: string }) => {
    const theme = useTheme();
    const isDesktop = useMediaQuery(theme.breakpoints.up('md'));

    return (
        <Card sx={{
            p: 0.5,
            pl: 0.8,
            pr: 0.8,
            display: 'flex',
            flexDirection: isDesktop ? 'row' : 'column',
            alignItems: 'center',
            gap: 1,
            bgcolor: color + "22",
            flex: 1, // This will make the cards equal width
            maxWidth: "120px"
        }}>
            <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', mb: isDesktop ? 0 : -0.75 }}>
                {icon}
            </Box>
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: isDesktop ? 'flex-start' : 'center' }}>
                <Typography variant="subtitle2" color="textSecondary" sx={{ mb: -0.75 }}>
                    {title}
                </Typography>
                <Typography variant="body1" style={{ color }}>
                    {capitalizeFirstLetter(value)}
                </Typography>
            </Box>
        </Card>
    );
};

const explanations = {
    "calculation": "GPT-4 is likely to get a numerical calculation wrong, especially a more complex one.",
    "counting": "GPT-4 struggles to count the number of 'things'.",
    "enumeration": "GPT-4 can can tend to miss things when enumerating (e.g. via summarizing all points made in a document).",
    "quotation": "GPT-4 is likely to recall quotes and/or attributions correctly.",
    "hallucination": "The model is likely to make information up, especially when it doesn't know the real answer.",
    "logic": "The model can fail at logic challenges, complex word problems, etc.",
}

const AnalysisSummary = ({ analysis }: { analysis: Analysis }) => {
    const { correctColor, completeColor, hallucinationColor } = getColor(analysis);
    // const { correctColor, completeColor, hallucinationColor, failureColor } = getColor(analysis);

    return (
        <Box sx={{
            width: "100%",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            // gap: 2,
        }}>
            <Box
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    gap: 2,
                    flexWrap: 'wrap',
                    width: '90%',
                    maxWidth: MAX_WIDTH
                }}
            >
                <AnalysisCard
                    title="Correctness"
                    value={analysis.correct}
                    color={correctColor}
                    icon={<CheckCircleOutlineIcon style={{ color: correctColor }} />}
                />
                <AnalysisCard
                    title="Completeness"
                    value={analysis.complete}
                    color={completeColor}
                    icon={<WarningAmberIcon style={{ color: completeColor }} />}
                />
                <AnalysisCard
                    title="Hallucination"
                    value={analysis.hallucinating}
                    color={hallucinationColor}
                    icon={<ErrorOutlineIcon style={{ color: hallucinationColor }} />}
                />
            </Box>
            <FailureCases analysis={analysis} />
        </Box>
    )
}

const FailureCases = ({ analysis }: { analysis: Analysis }) => {
    // If no failure cases, return null
    if (!analysis.failure_cases || Object.values(analysis.failure_cases).map((value) => value === "unlikely").every((value) => value === true)) {
        // return <p>No failures</p>
        return null;
    }

    return (
        <div style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            gap: 1,
            marginTop: "8px"
        }}>
            {analysis.failure_cases && Object.entries(analysis.failure_cases).map(([key, value], i) => {
                if (value === "unlikely") {
                    return <></>;
                }
                return (
                    <FailureCase
                        key={i}
                        title={capitalizeFirstLetter(key)}
                        value={key}
                        color={STATUS_MAP[getSeverityIndex(value)]}
                    />
                )
            })}
        </div>
    )
}

const FailureCase = ({ title, value, color }: { title: string, value: string | undefined, color: string }) => {
    return (
        <Chip label={
            <>
                <Tooltip title={explanations[value as keyof typeof explanations] || "Unknown"}>
                    <span>{title}</span>
                </Tooltip>
            </>
        } sx={{ bgcolor: color, color: "#e1e1e1", mr: 1 }} />
    )
}

const AnalysisNotes = ({ message }: { message: Message }) => {
    const notes = message.processing?.analysis?.issues;

    return (
        <Box sx={{ width: '90%', maxWidth: MAX_WIDTH }}>
            {notes && notes?.length > 0 && (
                <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1, alignItems: 'center', mt: 1 }}>
                    {notes.map((note: Note, i: number) => (
                        <NoteRenderer key={i} message={message} note={note} />
                    ))}
                </Box>
            )}
        </Box>

    )
}
