// Map note types to their respective icons
const ICON_MAP = {
    incorrect: ErrorOutlineIcon,
    incomplete: WarningAmberIcon,
    hallucination: ReportProblemIcon,
    failure_case: HighlightOffIcon,
    other: HelpOutlineIcon,
};

const NoteRenderer = ({ message, note }: { message: Message, note: Note }) => {
    const { actions } = useConversation();

    const sevIndex = getSeverityIndex(note.severity);
    const sevColor = STATUS_MAP[sevIndex as keyof typeof STATUS_MAP];
    const IconComponent = ICON_MAP[note.type] || HelpOutlineIcon; // Default to HelpOutlineIcon if type is not found

    return (
        <Card sx={{ width: '100%', maxWidth: MAX_WIDTH, p: 1.5, pb: 0.8, bgcolor: `${sevColor}22`, position: 'relative' }}>
            <Typography variant="body1" style={{ color: sevColor }}>
                {note.text}
            </Typography>
            <Typography variant="body2" style={{ marginTop: "4px", color: sevColor, display: 'flex', justifyContent: 'space-between' }}>
                <div style={{ width: "100px", display: 'flex', alignItems: 'center', gap: 8 }}>
                    <IconComponent style={{ color: sevColor }} />
                    {note.type ? capitalizeFirstLetter(note.type) : "Unknown"}
                </div>
                <Button
                    size="small"
                    style={{
                        height: "26px",
                        color: "#e1e1e1",
                        backgroundColor: "#1e1e1e",
                        borderColor: sevColor,
                        // cursor: "pointer"
                    }}
                    onClick={() => {
                        actions.reviseMessage(message.id, note);
                    }}
                >
                    Investigate
                </Button>
            </Typography>
        </Card >
    );
}