import React, { useEffect, createRef, useState, useMemo, useCallback } from 'react';
import { useConversation } from '../conversation/context';
import { Grid, List, ListItem, ListItemButton, ListItemText, Tooltip } from '@mui/material';
import ContentEditable from 'react-contenteditable';
import { useEditor, EditorContent } from '@tiptap/react'
import StarterKit from '@tiptap/starter-kit'
import { unified } from 'unified'
import remarkParse from 'remark-parse'
import remarkRehype from 'remark-rehype'
import rehypeStringify from 'rehype-stringify'
import './markdown.scss';
import { Message } from '../conversation/types';
import { getChildren } from '../conversation/tree';
import { NoteInterior } from './NoteInterior';
import { getColor } from './utility';
import "./markdown.scss"


export function MessageRenderer({ message, onChange, handleClick }: {
    message: Message;
    onChange: (text: string) => void;
    handleClick: (message: Message, recurse: boolean) => void;
}) {
    const { state, dispatch, actions, curConversation, curMessage } = useConversation();

    const [hovering, setHovering] = useState(false);
    const [modelPickerOpen, setModelPickerOpen] = useState(false);
    // console.log("Message node:", node);
    const editor = useEditor({
        extensions: [
            StarterKit,
        ],
        // content: message.text,
        content: message.text
    })


    useEffect(() => {
        if (!editor) {
            return;
        }

        // Reference: https://github.com/ueberdosis/tiptap/issues/2403#issuecomment-1017840603
        editor.off("update");
        editor.on("update", ({ editor: updatedEditor }) => onChange(updatedEditor.getHTML()));
    }, [editor, onChange]);

    useEffect(() => {
        // When the state changes, update the editor
        if (!editor) {
            return;
        }

        if (editor.getHTML() !== message.text) {
            // const raw = message.text;
            const raw = message.text;

            // Convert the text to HTML
            unified()
                .use(remarkParse)
                .use(remarkRehype)
                // .use(rehypeSanitize)
                .use(rehypeStringify)
                .process(raw)
                .then((file: any) => {
                    editor.commands.setContent(file.value);
                });
        }
    }, [message.text, editor]);

    const { totalColor } = getColor(message.processing?.analysis);

    return (
        <div>
            <div className="message-div" style={{
                backgroundColor: message.type === 'user' ? '#cacaca' : '#e0e0e0',
                marginBottom: message.type === 'user' ? '-4px' : '8px',
                borderRadius: '8px',
                padding: '8px',
                width: '100%',
            }}>
                <Grid container spacing={1}>
                    <Grid
                        onMouseEnter={() => setHovering(true)}
                        onMouseLeave={() => setHovering(false)}
                        item
                        xs={12}
                        style={{
                            marginBottom: "4px"
                        }}
                    >
                        <MessageHeader message={message} hovering={hovering} />
                    </Grid>
                    <Grid item xs={12} style={{
                        marginBottom: "-12px",
                    }}>
                        {message.type === "system" && (
                            // Simple text input, no markdown
                            <ContentEditable
                                // html={message.text}
                                html={message.text}
                                disabled={false}
                                onChange={(e: any) => onChange(e.target.value)}
                                style={{
                                    // Don't outline when focused
                                    outline: "none",
                                    marginBottom: "16px", whiteSpace: 'pre-wrap', width: "100%", height: "100%"
                                }}
                            />
                        )}
                        {message.type !== "system" && (
                            <EditorContent editor={editor} />
                        )}
                    </Grid>
                    <Grid item xs={12} style={{
                        marginBottom: "-12px",
                    }}>
                        <NoteInterior message={message} color={totalColor} />
                    </Grid>
                </Grid>
            </div>
            {
                message.id === state.currentMessageID && curConversation && getChildren(curConversation.tree, message.id).length > 0 && (
                    <List style={{ margin: "0px", padding: "0px" }}>
                        {getChildren(curConversation.tree, message.id).map((child) => {
                            return (
                                <MessageResponseItem
                                    key={child.id}
                                    message={child}
                                    handleClick={handleClick}
                                />
                            )
                        })}
                    </List>
                )
            }
        </div >
    );
}

const MessageHeader = ({ message, hovering }: { message: Message; hovering: boolean }) => {
    const { state, dispatch, actions, curConversation } = useConversation();
    return (
        <div
            style={{
                display: 'flex',
                justifyContent: 'space-between'
            }}
        >
            <div style={{ maxWidth: "50%", display: 'flex', justifyContent: 'flex-start', color: "#1e1e1e" }}>
                <b>{message.type[0].toUpperCase() + message.type.slice(1)}</b>
                {hovering && <MessageActions message={message} />}
            </div>

            <div style={{ maxWidth: "50%", display: 'flex', justifyContent: 'flex-start', color: "#1e1e1e" }}>
                <i>{message.processing?.summary}</i>
            </div>
        </div>
    );
}

const MessageActions = ({ message }: { message: Message }) => {
    const { state, dispatch, actions, curConversation } = useConversation();

    const styling = {
        marginLeft: "12px",
        // marginRight: "5px"
    };
    switch (message.type) {
        case "system":
            return <></>
        case "user":
            return (
                <div style={{
                    ...styling,
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "flex-end",
                    gap: "10px"
                }}>
                    <span
                        style={{ color: '#606060', justifyContent: 'flex-end' }}
                        onClick={(e: any) => {
                            e.stopPropagation();
                            dispatch({
                                type: 'delete-message',
                                uuid: message.id,
                            })
                        }}
                    >
                        <i>Delete</i>
                    </span>
                    <span
                        style={{ color: '#606060', justifyContent: 'flex-end' }}
                        onClick={(e: any) => {
                            e.stopPropagation();
                            actions.forkMessage(message);
                        }}
                    >
                        <i>Fork</i>
                    </span>
                    <span
                        style={{ color: '#606060', justifyContent: 'flex-end' }}
                        onClick={(e: any) => {
                            e.stopPropagation();
                            actions.getResponse(message.id);
                            // actions.rerunMessage(message);
                        }}
                    >
                        <i>Respond</i>
                    </span>
                </div>
            )
        case "response":
            return (
                <div style={styling}>
                    <span
                        style={{ color: '#606060', justifyContent: 'flex-end' }}
                        onClick={(e: any) => {
                            e.stopPropagation();
                            // actions.rerunMessage(message);
                            const parent = actions.getParent(message);
                            if (!parent) {
                                throw new Error("No parent found");
                            }
                            actions.getResponse(parent.id);
                        }}
                    >
                        <i>Retry</i>
                    </span>
                </div>
            )

        default:
            throw new Error("Unknown message type:", message.type);
    }
}

const MessageResponseItem = ({ message, handleClick }: {
    message: Message;
    handleClick: (node: Message, recurse: boolean) => void;
}) => {
    const { state, dispatch, actions, curConversation } = useConversation();
    const { getChildren, getParent } = actions;

    let summary = "";
    if (message.type !== "system") {
        summary = message.processing?.summary || "";
    }

    // const primary = message.summary ? message.summary : message.text.slice(0, 100) + "...";
    const primary = summary ? summary : message.text.slice(0, 100) + "...";

    const children = getChildren(message);
    // console.log("Found", children.length, "children");
    let secondary;
    // if (,?.children[0] && state.nodes[message.children[0]].summary) {
    if (children[0] && children[0].type !== "system" && children[0].processing?.summary) {
        // secondary = state.nodes[message.children[0]].summary;
        secondary = children[0].processing?.summary;
    } else if (children[0]) {
        // secondary = state.nodes[message.children[0]].text.slice(0, 100) + "...";
        secondary = children[0].text.slice(0, 100) + (children[0].text.length > 100 ? "..." : "");
    } else {
        secondary = "";
    }

    return (
        <ListItemButton
            style={{ marginLeft: "auto", marginRight: "auto", width: "calc(100% - 8px)", borderRadius: "4px" }}
            key={message.id}
            onClick={(e: any) => {
                e.stopPropagation();
                handleClick(message, e.shiftKey);
            }}
        >
            <ListItemText
                style={{ padding: "0px", margin: "0px" }}
                primary={primary}
                secondary={secondary}
            />
        </ListItemButton>
    );
}