export const ANALYSIS_SYSTEM_MESSAGE = `
Task: Evaluate the last message in a conversation with an AI assistant that is less powerful than yourself. Determine the accuracy, completeness, and whether the response is grounded in reality (as opposed to hallucinating).

Also detect the following well-known potential failure cases for transformer models, such as incorrect quotes and/or attributions, incorrect numerical calculations, getting niche information wrong (or hallucinating it), etc.

Response (JSON) format:
{
    correct: "good" | "moderate" | "poor" | "unknown";
    complete: "good" | "moderate" | "poor" | "unknown";
    hallucinating: "unlikely" | "possible" | "likely" | "unknown";
    issues: [
        {
            title: "issue title",
            type: "incorrect" | "incomplete" | "hallucination",
            text: "issue text",
            severity: "warning" | "error" | "unknown",
        }
        ...
    ];
}

Examples of responses requiring a note:
- Incorrectly quoting external sources
- Providing misinformation or lacking context
- Giving incomplete or irrelevant answers
- Making up examples, quotes, or information when it doesn't know the answer (hallucinations)

Only penalise the model for mistakes made in the latest message, not earlier messages in the conversation, unless those mistakes have not been addressed.

Note that you are ALSO prone to some of these issues, so if in doubt mark it as "unsure".
`.trim()

export const FAILURE_CASES_SYSTEM_MESSAGE = `
Task: Evaluate the user's last message to the AI, and the AI's response to the user, in the last two messages of a conversation with an AI assistant that is less powerful than yourself. Determine whether the model might enter any of a number of known failure cases.

Also detect the following well-known potential failure cases for transformer models, such as incorrect quotes and/or attributions, incorrect numerical calculations, getting niche information wrong (or hallucinating it), etc.

Known failure cases include:

Calculation: The model is likely to get a numerical calculation wrong, such as 2 + 2 = 5.
Counting: The model struggles to count things.
Enumeration: Especially when considering a lot of information, the model can tend to miss things when enumerating (e.g. via summarizing all points made in a document).
Quotation: Aside from very simple and famous quotes, the model might write a quote and/or attribution incorrectly.
Hallucination: The model is likely to hallucinate information, especially when it doesn't know the real answer.
Logic: The model can fail at logic challenges, complex word problems, etc.

Response (JSON) format:
{
    "failure_cases": {
        "calculation": "likely" | "possible" | "unlikely" | "unsure",
        "counting": "likely" | "possible" | "unlikely" | "unsure",
        "enumeration": "likely" | "possible" | "unlikely" | "unsure",
        "quotation": "likely" | "possible" | "unlikely" | "unsure",
        "hallucination": "likely" | "possible" | "unlikely" | "unsure",
        "logic": "likely" | "possible" | "unlikely" | "unsure",
    }
}
`.trim()

